import ErmjBaseHandler from "./ErmjBaseHandler";

export default class ErmjChangeFlowerHandler extends ErmjBaseHandler{
    protected execute(msg){
        let localSeat = this.SitHelper.serverSToLocalN(msg._src);
        let outArr = msg._para.flowers || [];
        let inArr = msg._para.new_cards || [];
        this.mainUI.onChangeFlower(localSeat, outArr, inArr, msg._para.left_count);
    }

    protected executeSync(msg){
        // 重连不会进这个协议, 合并到table_data
    }
}